<template>
    <card-item v-for="item in items" :card="item" @show-verification="showVerification(item)"></card-item>
    <mt-popup class="pop-view" :visible.sync="popupVisible" position="right">
        <verification-cards v-ref:vcards @cancel="cancelPop"></verification-cards>
    </mt-popup>
</template>
<script>
    import CardItem from './CardItem.vue';
    import VerificationCards from './VerificationCards.vue';
    import CardStore from './CardStore.js';

    export default {
        components: {
            CardItem,
            VerificationCards
        },
        data() {
            return {
                popupVisible: false,
                items: [1, 2, 3, 4, 5, 6, 7, 8, 9],
                name: '123'
            }
        },
        methods: {
            showVerification(item) {
                CardStore.detailList(item.card.id).then(res => {
                    if (res && res.length) {
                        this.$refs.vcards.refresh(res);
                        this.popupVisible = true;
                    }
                });
            },
            cancelPop() {
                this.popupVisible = false;
            },
            refresh() {
                CardStore.list().then(res => {
                    this.items = res || [];
                });
            }
        },
        ready() {
            CardStore.list().then(res => {
                this.items = res || [];
            });
        }
    }
</script>


