<style lang="less">
    .card-detail {
        margin: 10px;
        padding: 10px;
        .card-img {
            width: 100%;
            overflow: hidden;
            background: #FFF;
            border-radius: 6px;
            overflow: hidden;
            border-bottom: 1px dashed #ccc;
            text-align: center;
            font-size: 0px;
            img {
                width: 100%;
            }
        }
        p {
            padding: 5px 0px;
        }
        .card-detail-title {
            font-weight: bold;
            padding-top: 10px;
        }
        .card-detail-info {
            font-size: 14px;
            color: #ababab;
        }
    }
</style>
<template>
    <mt-header title="">
        <mt-button slot="left" icon="back" @click="cancel"></mt-button>
    </mt-header>
    <div class="major-content">
        <div class="card-detail">
            <div class="card-img">
                <img :src="'/JAMEKA_IMAGE/'+card.pic" onerror="this.src='/jameka-vm/images/nopic.png'" />
            </div>
            <p class="card-detail-title">{{card.title}}</p>
            <p class="card-detail-info">{{data.cardNo}}</p>
            <p class="card-detail-info">{{card.endTime | time 'yyyy-MM-dd HH:mm:ss'}}</p>
            <p class="card-detail-info">{{data.receiveTime | time 'yyyy-MM-dd HH:mm:ss'}}</p>
            <p>{{card.details}}</p>
        </div>
        <div class="text-center">
            <mt-button type="primary" @click="verification"></mt-button>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <mt-button type="default" @click="cancel"></mt-button>
        </div>
    </div>
</template>
<script>
    import CardItem from './CardItem.vue';
    import CardStore from './CardStore';

    export default {
        components: {
            CardItem
        },
        data() {
            return {
                items: [1, 2, 3, 4, 5, 6, 7, 8, 9],
                name: '123',
                data: {}
            }
        },
        computed: {
            card() {
                return this.data && this.data.card || {}
            }
        },
        methods: {
            show(data) {
                this.data = data;
            },
            verification() {
                CardStore.verification(this.data.cardNo).then(res => {
                    Base.Toast('');
                    this.$emit('cancel');
                });
            },
            cancel() {
                this.$emit('cancel');
            }
        }
    }
</script>


