<style lang="less">
    @import '../../style.less';
    .orders-item {
        margin: 5px;
        padding: 10px;
        background: #FFF;
        border-bottom: 1px solid @border;
        margin-bottom: 0.8rem;
        overflow: hidden;
        font-size: 12px;
        &:last-child {
            margin-bottom: 0rem;
        }
        &>p {
            padding-bottom: 10px;
            label {
                float: right;
                color: @warning;
            }
        }
        .orders-item-detail {
            position: relative;
            display: table;
            min-height: 80px;
            overflow: hidden;
            padding-bottom: 0px !important;
            .detail-image {
                display: table-cell;
                width: 40%;
                img {
                    width: 100%;
                }
            }
            .detail-info {
                display: table-cell;
                vertical-align: top;
                padding-left: 10px;
                p {
                    padding-bottom: 3px;
                }
                .price {
                    font-size: 14px;
                    text-align: right;
                }
            }
        }
        .order-total-info {
            font-size: 14px;
            div{
                padding-top:5px;
            }
        }
    }
</style>
<template>
    <div :class="['orders-item']">
        <p>{{order.tradeNo}}<label>{{orderStatus}}</label></p>
        <div class="orders-item-detail" v-for="tradeDetail in order.tradeDetails">
            <div class="detail-image">
                <img :src="'/JAMEKA_IMAGE/'+tradeDetail.skuDetail.image" onerror="this.src='/jameka-vm/images/nopic.png'" />
            </div>
            <div class="detail-info">
                <p>{{tradeDetail.skuDetail.name}}</p>
                <p class="text-sub">{{tradeDetail.skuDetail.attributes.join(' ')}}</p>
                <div>
                    <div class="price"><span>{{tradeDetail.skuDetail.virPrice}}</span>+{{tradeDetail.skuDetail.price}}</div>
                    <span style="float:right;">X{{tradeDetail.count}}</span>
                </div>
            </div>
        </div>
        <div class="order-total-info">
            &nbsp;<span class="text-info">{{countItemSku(order.tradeDetails)}}&nbsp;</span>&nbsp;&nbsp;&nbsp;
            <span class="text-info">{{order.totalVirPrice}}+{{order.totalPrice}}</span>
            <span v-if="order.preferential">&yen;{{order.preferential}}</span>
            <div class="text-sub" v-if="order.buyerType=='2'">{{order.merchant.address}}</div>
        </div>
    </div>
</template>
<script>
    const ORDER_STATUS = {
        '1': '',
        '2': '',
        '3': '',
        '4': '',
        '5': '',
        '6': '',
        '8': '',
        '9': ''
    };

    export default {
        props: {
            order: {}
        },
        data() {
            return {
                name: '123',
                card: {}
            }
        },
        computed: {
            orderStatus() {
                return ORDER_STATUS[this.order.status];
            }
        },
        methods: {
            countItemSku(items) {
                var count = 0;
                if (items && items.length) {
                    items.forEach(item => {
                        count += item.count;
                    })
                }
                return count;
            },
            showDetail() {
                this.$emit('show-verification', this.card);
            }
        }
    }
</script>


