<style lang="less">
    .sign-container {
        .title {
            text-align: center;
            font-size: 20px;
            padding: 50px 0px;
        }
        .sign-field {
            display: table;
            margin: 10px 0px;
            label {
                display: table-cell;
                width: 100px;
                text-align: right;
                padding-right: 20px;
            }
            &>div {
                display: table-cell;
                input {
                    border: 1px solid #efefef;
                    border-radius: 2px;
                    padding: 10px;
                }
            }
        }
    }
</style>
<template>
    <div class="sign-container">
        <div class="title"></div>
        <div class="sign-field">
            <label></label>
            <div class="sign-widget">
                <input type="text" v-model="model.name">
            </div>
        </div>
        <div class="sign-field">
            <label></label>
            <div class="sign-widget">
                <input type="password" v-model="model.password">
            </div>
        </div>
        <mt-button style="width:80%;margin:50px auto;" type="primary" size="large" @click="login"></mt-button>
    </div>
</template>
<script>
    import SignStore from './SignStore.js';

    export default {
        data() {
            return {
                model: {
                    name: '',
                    password: ''
                }
            };
        },
        methods: {
            login() {
                SignStore.login(this.model).then(res => {
                    this.$emit('logined', res);
                });
            }
        }
    }
</script>


