<style lang="less">

</style>
<template>
    <mt-header title="">
        <mt-button slot="left" icon="back" @click="cancel"></mt-button>
    </mt-header>
    <div class="major-content">
        <div class="card-item" v-for="item in items">
            <div class="card-head">
                <div class="card-title">{{item.card.title}}</div>
                <div class="card-date">{{item.card.startTime | time 'yyyy/MM/dd'}}{{item.card.endTime | time 'yyyy/MM/dd'}}</div>
                <div class="card-date" style="margin-top:5px;">{{item.receiveTime | time}}</div>
            </div>
        </div>
    </div>
</template>
<script>
    import CardItem from './CardItem.vue';

    export default {
        components: {
            CardItem
        },
        data() {
            return {
                items: [],
                name: '123'
            }
        },
        methods: {
            refresh(res){
                this.items = res;
            },
            cancel() {
                this.$emit('cancel');
            }
        }
    }
</script>


