<style>
    .verification-button {
        display: block;
        width: 80%;
        margin: 20px auto;
        height: 80px;
        line-height: 80px;
        background: #5bc0de;
        color: #FFF;
        border-radius: 10px;
        font-size: 18px;
        border: none;
    }
    
    .verification-button.green {
        background: #5cb85c;
    }
</style>
<template>
    <div class="">
        <button class="verification-button" @click="verificationOrder('order')">
            
        </button>
        <button class="verification-button green" @click="verificationCard('card')">
            
        </button>
        <mt-popup class="pop-view" :visible.sync="popupVisible" position="right">
            <div v-show="showPop=='order'">
                <order-detail v-ref:orderdetail @cancel="hidePop"></order-detail>
            </div>
            <div v-show="showPop=='card'">
                <card-detail v-ref:carddetail @cancel="hidePop"></card-detail>
            </div>
        </mt-popup>
    </div>
</template>
<script>
    import CardStore from '../card/CardStore';
    import OrderStore from '../order/OrderStore';

    import OrderDetail from '../order/OrderDetail.vue';
    import CardDetail from '../card/CardDetail.vue';

    export default {
        components: {
            OrderDetail,
            CardDetail
        },
        data() {
            return {
                showPop: 'card',
                popupVisible: false
            }
        },
        methods: {
            verificationOrder() {
                Sunset.Wechat.cmd('scanQRCode').then(res => {
                    OrderStore.getByNo(res.split(',').pop()).then(data => {
                        this.showPop = 'order';
                        this.$refs.orderdetail.show(data);
                        this.popupVisible = true;
                    });
                });
            },
            verificationCard() {
                Sunset.Wechat.cmd('scanQRCode').then(res => {
                    CardStore.getCard(res.split(',').pop()).then(data => {
                        this.showPop = 'card';
                        this.$refs.carddetail.show(data);
                        this.popupVisible = true;
                    });
                });
            },
            hidePop() {
                this.popupVisible = false
            }
        }
    }
</script>


