<style lang="less">
    .order-container {
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
        .order-wrap {
            position: absolute;
            top: 90px;
            bottom: 0px;
            left: 0px;
            right: 0px;
            overflow-x: hidden;
            overflow-y: auto;
        }
        .filter-wrap {
            padding: 5px;
            box-sizing: border-box;
            .datepickbox {
                width: 100px;
            }
            select {
                margin-left: 15px;
                width: 100px;
                display: inline-block;
                padding: 3px;
                text-align: center;
                line-height: 22px;
                font-size: 16px;
                border: 2px solid #fff;
                box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
                border-radius: 2px;
                color: #5F5F5F;
                box-sizing: border-box;
            }
        }
    }
</style>
<template>
    <div class="order-container">
        <div class="filter-wrap">
            <date-picker :time.sync="startTime" :option="dateOptions('')" @change="listOrders(false,true)" :limit="limit"></date-picker>
            -
            <date-picker :time.sync="endTime" :option="dateOptions('')" @change="listOrders(false,true)" :limit="limit"></date-picker>
            <select v-model="status" @change="listOrders(false,true)">
                <option value="2"></option>
                <option value="6"></option>
            </select>
        </div>
        <mt-navbar :selected.sync="currentTab">
            <mt-tab-item id="goods" @click="listOrders(1,true)"></mt-tab-item>
            <mt-tab-item id="activity" @click="listOrders(2,true)"></mt-tab-item>
        </mt-navbar>
        <!-- tab-container -->
        <div class="order-wrap">
            <mt-tab-container :active.sync="currentTab">
                <mt-tab-container-item id="goods">
                    <order-item v-for="item in items" :order="item"></order-item>
                    <mt-button v-show="showLoadMore" style="margin-bottom:15px;" type="primary" size="large" @click="loadMore"></mt-button>
                </mt-tab-container-item>
                <mt-tab-container-item id="activity">
                    <order-item v-for="item in items" :order="item"></order-item>
                    <mt-button v-show="showLoadMore" style="margin-bottom:15px;" type="primary" size="large" @click="loadMore"></mt-button>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>
</template>
<script>
    import OrderItem from './OrderItem.vue';
    import DatePicker from 'vue-datepicker/vue-datepicker-1.vue';

    import OrderStore from './OrderStore.js';

    var now = new Date();
    now.setDate(1);
    const firstDay = Sunset.Dates.format(now, 'yyyy-MM-dd');
    const today = Sunset.Dates.format(new Date(), 'yyyy-MM-dd');
    const tomorow = Sunset.Dates.format(new Date(new Date().getTime() + 86400000), 'yyyy-MM-dd');

    export default {
        components: {
            OrderItem,
            DatePicker
        },
        data() {
            return {
                startTime: firstDay,
                endTime: today,
                status: '2',
                currentType: null,
                currentTab: 'goods',
                popupVisible: false,
                items: [],
                limit: [{
                    type: 'fromto',
                    to: tomorow
                }],
                loading: false,
                pageNumber: 1,
                showLoadMore: true
            }
        },
        computed: {

        },
        methods: {
            dateOptions(placeholder) {
                return {
                    type: 'day',
                    week: ['', '', '', '', '', '', ''],
                    month: ['', '', '', '', '', '', '', '', '',
                        '', '', ''
                    ],
                    format: 'YYYY-MM-DD',
                    placeholder: placeholder,
                    inputStyle: {
                        'width': '100%',
                        'display': 'inline-block',
                        'padding': '3px',
                        'text-align': 'center',
                        'line-height': '22px',
                        'font-size': '16px',
                        'border': '2px solid #fff',
                        'box-shadow': '0 1px 3px 0 rgba(0, 0, 0, 0.2)',
                        'border-radius': '2px',
                        'color': '#5F5F5F',
                        'box-sizing': 'border-box'
                    },
                    color: {
                        header: '#3091f2',
                        headerText: '#fff'
                    },
                    buttons: {
                        ok: '',
                        cancel: ''
                    },
                    overlayOpacity: 0.5, // 0.5 as default
                    dismissible: true // as true as default
                };
            },
            start() {
                this.$refs.start.open();
            },
            showVerification(data) {
                this.popupVisible = true;
            },
            cancelPop() {
                this.popupVisible = false;
            },
            listOrders(type, refresh) {
                if (refresh) {
                    this.pageNumber = 1;
                    this.items = [];
                    this.showLoadMore = true;
                    Base.Indicator.open();
                }
                var pageNumber = this.pageNumber;
                var filter = {
                    pageNumber: pageNumber,
                    type: type ? (this.currentType = type) : this.currentType,
                    status: this.status,
                    startTime: Sunset.Dates.parse(this.startTime).getTime(),
                    endTime: Sunset.Dates.parse(this.endTime).getTime() + 86399999
                };
                OrderStore.list(filter).then(res => {
                    this.items = this.items.concat(res.data);
                    if (this.items.length >= res.count) {
                        this.showLoadMore = false;
                    }
                    Base.Indicator.close();
                }).catch(e => {
                    Base.Indicator.close();
                });
            },
            refresh() {
                this.listOrders(1);
            },
            loadMore() {
                this.pageNumber += 1;
                this.listOrders();
            }
        },
        ready() {}
    }
</script>


