import SignStore from '../sign/SignStore';

const URLS = {
    LIST: '/service/sale/trade/list',
    GET_BY_NO: '/service/sale/trade/getByTradeNo/{tradeNo}',
    VERIFICATION: '/service/sale/trade/byself/{id}'
}

module.exports = {
    list(filter) {
        var currentUserCache = SignStore.currentUserCache;
        return $http({
            url: URLS.LIST,
            data: {
                currentPage: filter.pageNumber,
                pageSize: 20,
                type: filter.type,
                startTime: filter.startTime,
                endTime: filter.endTime,
                status : filter.status,
                franchiseeId: currentUserCache.user.franchisee && currentUserCache.user.franchisee.id,
                storeId: currentUserCache.user.store && currentUserCache.user.store.id,
                merchantId: currentUserCache.user.merchant && currentUserCache.user.merchant.id
            }
        }).then(data => {
            try {
                data && data.data && data.data.forEach(item => {
                    item.tradeDetails.forEach(item => {
                        item.skuDetail = JSON.parse(item.skuJSON);
                    });
                });
            } catch (e) {}
            return data;
        });
    },
    getByNo(tradeNo) {
        return $http({
            url: URLS.GET_BY_NO.replace(/\{tradeNo\}/, tradeNo)
        }).then(data => {
            var trade = data && data.trade || data;
            try {
                trade.tradeDetails.forEach(item => {
                    item.skuDetail = JSON.parse(item.skuJSON);
                });
            } catch (e) {}
            return trade;
        });
    },
    verification(id) {
        return $http({
            url: URLS.VERIFICATION.replace(/\{id\}/, id)
        });
    }
}


