import SignStore from '../sign/SignStore';

const URLS = {
    GET_CARD: '/service/sale/card/scan/{userId}/{cardNo}',
    VERIFICATION: '/service/sale/card/use/{userId}/{cardNo}',
    LIST: '/service/sale/card/stat/{userId}/{cardType}',
    DETAIL_LIST: '/service/sale/card/list'
};

module.exports = {
    list() {
        return SignStore.currentUser().then(user => {
            return Promise.all([
                $http({
                    url: URLS.LIST.replace(/\{userId\}/, user.user.id).replace(/\{cardType\}/, '1')
                }),
                $http({
                    url: URLS.LIST.replace(/\{userId\}/, user.user.id).replace(/\{cardType\}/, '2')
                })
            ]).then(res => {
                return res[0].concat(res[1]);
            });
        });
    },
    detailList(cardId) {
        return $http({
            url: URLS.DETAIL_LIST,
            type: 'POST',
            data: {
                cardId,
                useStatus: '1'
            }
        }).then(res => {
            return res && res.data || [];
        });
    },
    getCard(cardNo) {
        return SignStore.currentUser().then(user => {
            return $http({ //186
                url: URLS.GET_CARD.replace(/\{userId\}/, user.user.id).replace(/\{cardNo\}/, cardNo)
            });
        });
    },
    verification(cardNo) {
        return SignStore.currentUser().then(user => {
            return $http({
                url: URLS.VERIFICATION.replace(/\{userId\}/, user.user.id).replace(/\{cardNo\}/, cardNo)
            });
        });
    }
}


