const URLS = {
    LOGIN: '/service/system/login',
    CURRENT: '/service/system/current',
    LOGOUT: '/service/system/logout'
}

export default {
    currentUserCache: null,
    login(model) {
        model.applicationId = 1;
        model.type = 1;
        return $http({
            url: URLS.LOGIN,
            type: 'GET',
            data: model
        }).then(data => {
            return this.currentUserCache = data;
        });
    },
    currentUser() {
        return Promise.resolve().then(() => {
            if (this.currentUserCache) {
                return this.currentUserCache;
            } else {
                return $http({
                    url: URLS.CURRENT,
                    type: 'GET'
                }).then(data => {
                    return this.currentUserCache = data;
                });
            }
        });
    },
    logout() {
        return $http({
            url: URLS.LOGOUT
        }).then(data => {
            return this.currentUserCache = null;
        });
    }
};


