<style lang="less">
    @import '../../style.less';
    .card-item {
        position: relative;
        margin: 8px;
        background: #FFF;
        .card-head {
            border-bottom: 1px dashed @border;
            padding: 10px;
            .card-title {
                font-weight: bold;
                font-size: 16px;
                color: @title;
                margin-bottom: 10px;
            }
            .card-date {
                font-size: 12px;
                color: @sub;
            }
        }
        .card-statistic {
            padding: 10px 0px;
            display: table;
            width: 100%;
            &>div {
                width: 33%;
                display: table-cell;
                text-align: center;
                border-right: 2px solid @border;
                &>label {
                    font-size: 14px;
                }
                &:last-child {
                    position: relative;
                    border-right: none;
                    padding-right: 10px;
                    i {
                        position: absolute;
                        right: 5px;
                        top: 30%;
                        font-size: 24px;
                    }
                }
                div {
                    padding-top: 5px;
                }
            }
        }
        &:before,
        &:after {
            content: '';
            position: absolute;
            top: 50%;
            margin-top: -10px;
            width: 20px;
            height: 20px;
            border-radius: 10px;
            background: #f5f7f9;
        }
        &:before {
            left: -10px;
        }
        &:after {
            right: -10px;
        }
    }
</style>
<template>
    <div class="card-item">
        <div class="card-head">
            <div class="card-title">{{cardInfo.title}}</div>
            <div class="card-date">{{cardInfo.startTime | time 'yyyy/MM/dd'}}{{cardInfo.endTime | time 'yyyy/MM/dd'}}</div>
        </div>
        <div class="card-statistic">
            <div class="text-info">
                <label></label>
                <div>{{cardInfo.number}}</div>
            </div>
            <div class="text-success">
                <label></label>
                <div>{{card.total}}</div>
            </div>
            <div class="text-warning" @click="showDetail">
                <label></label>
                <div>{{card.user}}</div>
                <i class="fa fa-angle-right"></i>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        props: {
            card: {}
        },
        data() {
            return {
                name: '123'
            }
        },
        computed: {
            cardInfo() {
                return this.card && this.card.card || {};
            }
        },
        methods: {
            showDetail() {
                this.$emit('show-verification', this.card);
            }
        }
    }
</script>


