<style lang="less">
	@import './style.less';
	* {
		padding: 0px;
		margin: 0px;
	}
	
	.major {
		position: absolute;
		left: 0px;
		right: 0px;
		top: 40px;
		bottom: 55px;
		overflow-x: hidden;
		overflow-y: auto;
		background: @background;
		color: @content;
	}
	
	.major-content {
		position: absolute;
		left: 0px;
		right: 0px;
		top: 40px;
		bottom: 0px;
		overflow-x: hidden;
		overflow-y: auto;
		background: @background;
		color: @content;
	}
	
	.pop-view {
		width: 100%;
		height: 100%;
		background-color: #fff;
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.mint-toast {
		z-index: 9999;
	}
</style>
<template>
	<div id="app">
		<div v-show="!logined">
			<mt-header title="">
			</mt-header>
			<sign @logined="userLogined"></sign>
		</div>
		<div v-show="logined">
			<mt-header :title="titles[currentPage]">
				<mt-button slot="right" @click="quit"><i class="icon-quit"></i></mt-button>
			</mt-header>
			<mt-tab-container class="major" :active.sync="currentPage">
				<mt-tab-container-item id="verification">
					<verification></verification>
				</mt-tab-container-item>
				<mt-tab-container-item id="orderView">
					<order-view v-ref:ordersview></order-view>
				</mt-tab-container-item>
				<mt-tab-container-item id="cardView">
					<card-view v-ref:cardsview></card-view>
				</mt-tab-container-item>
			</mt-tab-container>
			<mt-tabbar :selected.sync="currentPage">
				<mt-tab-item id="verification">
					<img slot="icon" src="/jameka-vm/images/person_mark.png"> 
				</mt-tab-item>
				<mt-tab-item id="orderView" @click="refreshOrders">
					<img slot="icon" src="/jameka-vm/images/person_oem.png"> 
				</mt-tab-item>
				<mt-tab-item id="cardView" @click="refreshCards">
					<img slot="icon" src="/jameka-vm/images/person_card.png"> 
				</mt-tab-item>
			</mt-tabbar>
		</div>
	</div>
</template>
<script>
	import {
		Toast,
		MessageBox,
		Indicator
	} from 'mint-ui';

	window.Base = {
		Toast: Toast,
		MessageBox: MessageBox,
		Indicator: Indicator
	}

	import Sign from './components/sign/Sign.vue';
	import Verification from './components/verification/Verification.vue';
	import OrderView from './components/order/OrderView.vue';
	import CardView from './components/card/CardView.vue';

	import SignStore from './components/sign/SignStore.js';

	export default {
		components: {
			Sign,
			Verification,
			OrderView,
			CardView
		},
		data() {
			return {
				logined: false,
				currentPage: 'verification',
				sheetVisible: false,
				titles: {
					verification: '',
					orderView: '',
					cardView: ''
				}
			}
		},
		methods: {
			userLogined(res) {
				this.logined = true;
				Sunset.Wechat.configParams = {
					storeId: res.user.store && res.user.store.id
				};
			},
			quit() {
				SignStore.logout().then(res => {
					this.logined = false;
				});
			},
			refreshOrders() {
				this.$refs.ordersview.refresh();
			},
			refreshCards() {
				this.$refs.cardsview.refresh();
			}
		},
		ready() {
			SignStore.currentUser().then(res => {
				this.userLogined(res);
			});
		}
	}
</script>
<style>
	body {
		font-family: Helvetica, sans-serif;
	}
</style>


