<template>
    <mt-header title="">
        <mt-button slot="left" icon="back" @click="cancel"></mt-button>
    </mt-header>
    <div class="major-content">
        <order-item :order="order"></order-item>
        <div class="text-center">
            <mt-button type="primary" @click="verification"></mt-button>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <mt-button type="default" @click="cancel"></mt-button>
        </div>
    </div>
</template>
<script>
    import OrderItem from './OrderItem.vue';
    import OrderStore from './OrderStore';

    export default {
        components: {
            OrderItem
        },
        data() {
            return {
                order: {}
            }
        },
        methods: {
            show(data) {
                this.order = data;
            },
            verification() {
                OrderStore.verification(this.order.id).then(res => {
                    Base.Toast('');
                    this.$emit('cancel');
                });
            },
            cancel() {
                this.$emit('cancel')
            }
        }
    }
</script>


